.newCard {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);

  &__content {
    background-color: #e7eb00;
    margin: 15% auto;
    padding: 20px;
    border: 1px solid #888;
    width: 40%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;

    &__buttons {
      padding-top: 10px;
      text-align: center;
    }
  }
}

.buttons {
  padding-top: 10px;
  text-align: center;

  &__add {
    background-color: #657514;
    margin-right: 5px;

    &:hover,
    :focus {
      transform: scale(105%);
    }
  }

  &__cancel {
    background-color: #dae892;

    &:hover,
    :focus {
      transform: scale(105%);
    }
  }
}
